<template>

	<view class="content">
		<topbar bg_color="#FFFFFF" text_color="#fff" :have_top="true">
			<text class="icon-fanhui iconfont" style="color: #999999;" @click="fanhui"> </text>
			<text class=" " style="color: #FFFFFF;"></text>
			<text class="pr5 fs14" style="color: #FFFFFF;"> </text>
		</topbar>
		<view class="pl30 pt25 fs20 fwb">
			注册
		</view>
		<view class="pl30 pt35 pr30 mt12">
			<view class="flex b-b-1">
				<text class="icon-yonghu iconfont c2"></text>
				<!-- <input  class="pupu_shouj" type="text" placeholder="用户名/手机号" placeholder-class="c2 pl10 fs16"/> -->
				<input placeholder-class="c2 fs16 pupu_shouj" type="text" placeholder="用户名/手机号"
					class="pupu_shouj c2 pl10 fs16" />
			</view>
			<view class="flex b-b-1 pt30">
				<text class="icon-mima1 iconfont c2 fwb"></text>

				<!-- <input  class="pupu_shouj" :type="seen ? type_text : type_password" placeholder="密码" placeholder-class="c2 pl10 fs16 pupu_shouj" placeholder-style="width: 200rpx;"/> -->
				<input placeholder-class="c2 fs16 pupu_shouj " :type="seen ? type_text : type_password"
					class="pupu_shouj c2 pl10 fs16" placeholder="密码" />
				<!--以下为两个小眼睛按钮，点击的时候跟换input里面的type属性-->
				<view v-show='seen' @click="changeSeen">
					<text class="icon-zhengyan iconfont c2"></text>
				</view>
				<view v-show='!seen' @click="changeSeen">
					<text class="icon-biyan iconfont c2"></text>
				</view>

			</view>
			<view class="flex b-b-1 pt30">
				<text class="icon-mima1 iconfont c2 fwb"></text>
				<input placeholder-class="c2  fs16 " type="text" class="pupu_shoujs c2 pl10 fs16" placeholder="验证码" />
				<button @click='codeClick' v-if='changess' class="fs12 bgcf" style="color: #FF1919;"> 获取验证码</button>
				<button @click='codeClick' :disabled='disabled' v-else class="fs12 "
					style="background-color: #ffffff;">{{second}}后重试</button>
			</view>
		</view>
		<view class="funs flex aic ju_c cf ml35 mt35">
			注册
		</view>
		<view class="pl35  ml12 pt12">
			<text class="fs14 c9" >注册代表您已同意</text>
			<text class="fs14" style="color: #FF1919;"  @click='jump' data-url='../About/About?id=3' :data-shifou='true'>《用户注册协议》</text>
			<text class="fs14" style="color: #FF1919;"  @click='jump' data-url='../About/About?id=2' :data-shifou='true'>《隐私协议》</text>
		</view>
	</view>
</template>

<script>
	import api from '../../components/service.js';
	var that
	import {
		mapState,
		mapMutations
	} from 'vuex'
	export default {
		name: "login",
		data() {
			return {
				showText: true,
				second: 59,
				disabled: false,
				changess: true,
				usr_pwd: "",
				seen: false,
				type_text: "text",
				type_password: "password",
			};
		},
		onHide() {

		},
		onLoad() {
			that = this
		},
		onShow() {

		},

		computed: {
			...mapState(['hasLogin', 'forcedLogin', 'userName', 'userinfo'])
		},
		methods: {
			...mapMutations(['logout', 'login']),
			getimg(img) {
				return api.getimg(img)
			},
			jump(e) {
				return api.jump(e)
			},
			fanhui() {
				wx.navigateBack({
					delta: 1
				})
			},
			changeSeen() {
				this.seen = !this.seen;
			},
			// 获取验证码
			codeClick() {

				that.changess = false;
				that.disabled = true
				// console.log("1")
				that.showText = false
				var interval = setInterval(() => {
					--that.second
				}, 1000)

				setTimeout(() => {
					clearInterval(interval)
					that.showText = true
					that.second = 60
					that.disabled = false
					that.changess = true;
				}, 60000)


			},
		}
	}
</script>

<style>
	.c2 {
		color: #c2c2c2;
	}

	page {
		background-color: #fff;
		width: 100%;
		height: 100%;
		margin: 0;
		padding: 0;
	}

	.content {
		background-color: #fff;
		width: 100%;
		height: 100%;
		margin: 0;
		padding: 0;
	}

	.funs {
		width: 600rpx;
		height: 90rpx;
		background: #FF1919;
		border-radius: 45rpx;
	}
</style>
